
import { Demo300GA } from '..';
import { CanvasWrapper } from './CanvasWrapper';
export class GraphicsCoreWrapper {
    app: Demo300GA;
    canvasWrapper: CanvasWrapper;
    constructor(app: Demo300GA) {
        this.app = app;
        this.canvasWrapper = new CanvasWrapper(this);
    }
    Resize(width: number, height: number) {
        this.canvasWrapper.canvasThree.ResizeCanvas(width, height);
    }
    Dispose() {
        this.canvasWrapper.canvasThree.isActive = false;
    }
}