
import { BoundingBox } from '../../AppGridOptimizer/Optimizer/Geometry/BoundingBox';
import { NVector3 } from '../../../../lib';

export class Polyline {

    public static GetPolylineTemp() {
        const pl = new Polyline();
        pl.Add(10, 10);
        pl.Add(-5, 8);
        pl.Add(-7, -10);
        pl.Add(-1, -8);
        pl.Add(10, 10);
        return pl;
    }
    boundingBox: BoundingBox;
    vertices: NVector3[] = [];
    constructor() {
        this.boundingBox = new BoundingBox();
    }
    public Add(x: number, y: number, z: number = 0) {
        this.vertices.push(new NVector3(x, y, z));
        this.boundingBox.Update(this.vertices);
    }
    public GetVerticesForThree() {
        const outList: number[][] = [];

        this.vertices.forEach((e: NVector3) => {
            outList.push([e.x, e.z, e.y]);
        });
        return outList;
    }
    public GetCenterVec() {
        const cv = new NVector3(0, 0, 0);
        for (let i = 0; i < this.vertices.length - 1; ++i) {
            cv.x += this.vertices[i].x;
            cv.y += this.vertices[i].y;
            cv.z += this.vertices[i].z;
        }
        cv.x /= this.vertices.length - 1;
        cv.y /= this.vertices.length - 1;
        cv.z /= this.vertices.length - 1;
        return cv;
    }
}