// import * as THREE from 'three';

import { Optimizer } from './Optimizer';

import { Definition } from '..';
import { NVector3 } from '../../../lib';

export class AppGridOptimizer {
    definition: Definition;
    optimizer: Optimizer;
    constructor(definition: Definition) {
        this.definition = definition;
        this.optimizer = new Optimizer(this, 15, 8);
        this.AddPolyline();
        this.AddPoints();
        this.AddRects();
    }
    public AddPolyline() {
        this.definition.renderer.AddPolyline(this.optimizer.polyline.GetVerticesForThree());
    }
    public AddPoints() {
        this.optimizer.gridSystem.pts.forEach((p: NVector3) => {
            this.definition.renderer.AddPoint([p.x, p.z, p.y], p.color);
        });
    }
    public AddRects() {
        for (let j = 0; j < this.optimizer.gridSystem.cells.length; ++j) {
            for (let i = 0; i < this.optimizer.gridSystem.cells[j].length; ++i) {
                const vert = this.optimizer.gridSystem.cells[j][i].GetVerticesForThree();
                if (vert) {
                    this.definition.renderer.AddRect(vert, this.optimizer.gridSystem.cells[j][i].color);
                }
            }
        }
    }
}