import { SceneWrapper } from '..';
import { NVector3 } from '../../lib';
import * as THREE from 'three';
export class PointInersection {
    vec: NVector3;
    data: THREE.Intersection;
    constructor(x: number, y: number, z: number) {
        this.vec = new NVector3(x, y, z);
    }
}
