import { MouseEventData } from './IO/MouseInteraction/MouseInteraction';
import { Asset } from './Asset';
import { ViewWrapper } from './ViewWrapper';
import { GraphicsCoreWrapper } from './GraphicsCoreWrapper';
import { SceneWrapper } from './SceneWrapper';
import { IO } from './IO';

import { initTestingNum, initTesting, initTestingNetwork } from './lib';

export class Demo300GA {
    divHost: HTMLElement;
    asset: Asset;
    viewWrapper: ViewWrapper;
    graphicsCoreWrapper: GraphicsCoreWrapper;
    sceneWrapper: SceneWrapper;
    io: IO;
    public static Init(id: string) {
        new Demo300GA(id);
    }
    constructor(id: string) {
        const div = document.getElementById(id);
        if (div) { this.divHost = div; }

        this.asset = new Asset();
        this.viewWrapper = new ViewWrapper(this);
        this.graphicsCoreWrapper = new GraphicsCoreWrapper(this);
        this.sceneWrapper = new SceneWrapper(this);
        this.io = new IO(this);

        initTesting('NJ3N6am8j');
        initTestingNum(4);
        initTestingNetwork();
        // console.log('GA..');

    }
    MouseEventController(e: MouseEventData) {
        this.sceneWrapper.activeCommandController.MouseEventController(e);
    }
    Resize(width: number, height: number) {
        this.graphicsCoreWrapper.Resize(width, height);
    }
    Dispose() {
        this.graphicsCoreWrapper.Dispose();
        this.sceneWrapper.Dispose();
    }
    Dummy() {/* */ }
}

export class Solution {
    public divHost: HTMLElement;

    constructor(id: string ) {
        this.divHost = document.getElementById(id) as HTMLDivElement;

        Demo300GA.Init(id)
    }
    public destroy() {
        if (this.divHost) {
            try {
                while (this.divHost.firstChild) {
                    this.divHost.removeChild(this.divHost.firstChild);
                }
            } catch (error) {
                console.error('Error in destroy method:', error);
            }
        } else {
            console.warn('divHost is undefined in destroy method');
        }
    }
}