import * as THREE from 'three';
import { Definition } from '../..';

export class Renderer {
    definition: Definition;

    constructor(definition: Definition) {
        this.definition = definition;
    }

    public AddPolyline(vertices: number[][], color: string = 'red') {
        const geometry = new THREE.BufferGeometry();
        const positions = new Float32Array(vertices.flatMap(v => v));
        geometry.setAttribute('position', new THREE.BufferAttribute(positions, 3));

        const material = new THREE.LineBasicMaterial({
            color: color,
            linewidth: 1,
            transparent: true,
            opacity: 0.55,
        });

        const line = new THREE.Line(geometry, material);
        this.definition.scene.add(line);
    }

    public AddRect(vertices: number[][], color: string = 'red') {
        const geometry = new THREE.BufferGeometry();
        const positions = new Float32Array(vertices.flatMap(v => v));
        geometry.setAttribute('position', new THREE.BufferAttribute(positions, 3));

        const material = new THREE.LineBasicMaterial({
            color: color,
            linewidth: 5,
        });

        const line = new THREE.Line(geometry, material);
        this.definition.scene.add(line);
    }

    public AddRectMesh(vertices: number[][], color: string = 'red') {
        const geometry = new THREE.BufferGeometry();
        const positions = new Float32Array(vertices.flatMap(v => v));
        geometry.setAttribute('position', new THREE.BufferAttribute(positions, 3));

        // 인덱스를 사용하여 두 개의 삼각형 정의
        const indices = new Uint16Array([0, 1, 2, 0, 2, 3]);
        geometry.setIndex(new THREE.BufferAttribute(indices, 1));

        const material = new THREE.MeshBasicMaterial({
            color: 0xffff00,
            side: THREE.DoubleSide
        });

        const mesh = new THREE.Mesh(geometry, material);
        this.definition.scene.add(mesh);
    }

    public AddPoint(vert: number[], color: string = '#00afaf') {
        const geometry = new THREE.BufferGeometry();
        geometry.setAttribute('position', new THREE.Float32BufferAttribute(vert, 3));

        const material = new THREE.PointsMaterial({
            size: 4,
            sizeAttenuation: false,
            color: color
        });

        const point = new THREE.Points(geometry, material);
        this.definition.scene.add(point);
    }
}