import { NVector3 } from '../../../../lib';
import { AppRectOptimizer } from '../AppRectOptimizer';
export class Optimizer {
    appRectOptimizer: AppRectOptimizer;

    rectAgent: RectAgent;
    cv: NVector3;
    constructor(appRectOptimizer: AppRectOptimizer) {
        this.appRectOptimizer = appRectOptimizer;
        this.cv = this.GetInitPoint();

        this.InitAgent();

        this.AddRenderer();
    }
    public GetInitPoint() {
        this.cv = this.appRectOptimizer.polyline.GetCenterVec();


        return this.cv;
    }
    public InitAgent() {
        this.rectAgent = new RectAgent(this.cv);
        this.rectAgent.Update(this.appRectOptimizer.polyline.vertices);
    }

    public AddRenderer() {
        this.appRectOptimizer.definition.renderer.AddPoint([this.cv.x, this.cv.z, this.cv.y]);
        this.appRectOptimizer.definition.renderer.AddRect(this.rectAgent.GetVerticesForThree());
    }
}


export class RectAgent {
    v: NVector3;
    vs: NVector3[] = [];
    widthHalf: number = 2;
    heighthHalf: number = 2;
    constructor(v: NVector3) {
        this.v = v;
        this.vs.push(
            new NVector3(v.x - this.widthHalf, v.y - this.heighthHalf),
            new NVector3(v.x + this.widthHalf, v.y - this.heighthHalf),
            new NVector3(v.x + this.widthHalf, v.y + this.heighthHalf),
            new NVector3(v.x - this.widthHalf, v.y + this.heighthHalf),
        );
    }
    public Update(vs: NVector3[]) {
        // this.Rotation(15);

        // let v0 = this.vs[0];
        let v1 = this.vs[1];
        let v2 = this.vs[2];
        // let v3 = this.vs[3];

        let optimal = 0;
        let areas = [];
        for (let i = 0; i < 360; i += 5) {
            optimal = i;
            this.Rotation(i);
            let sefeguard = 0;
            while (this.Check(vs)) {
                this.RotateVec(v1, i, 1.1, 1);
                this.RotateVec(v2, i, 1.1, 1);
                // v0.x -= 0.1;
                // v3.x -= 0.1;

                v1.x += 0.1;
                v2.x += 0.1;
                if (sefeguard++ > 1000) {
                    break;
                }
            }
            areas.push(this.GetArea());
        }

        console.log(areas);
        this.Rotation(optimal);


    }
    public GetArea() {
        return NVector3.GetArea(this.vs);
    }
    public Rotation(v: number) {
        this.vs = NVector3.RotateVecsLoal(this.vs, v);
    }
    public RotateVec(v: NVector3, angle: number, xOff: number, yOff: number) {
        let cos = Math.cos(angle * (3.14159265358979 / 180.0));
        let sin = Math.sin(angle * (3.14159265358979 / 180.0));
        v.x = v.x * xOff * cos - v.y * sin;
        v.y = v.x * yOff * sin + v.y * cos;
    }
    public Check(vs: NVector3[]) {
        return NVector3.IsInsideOfVecsWtihVecs(this.vs, vs);
    }
    public GetVerticesForThree() {
        return [
            [this.vs[0].x, 0, this.vs[0].y],
            [this.vs[1].x, 0, this.vs[1].y],
            [this.vs[2].x, 0, this.vs[2].y],
            [this.vs[3].x, 0, this.vs[3].y],
            [this.vs[0].x, 0, this.vs[0].y],
        ];
    }

}


export const RotateVecsLoal = (vecs: NVector3[], angle: number): NVector3[] => {
    let cptToLocal = NVector3.GetCenterVecFromVecs(vecs);
    let cptToWorld = new NVector3(-cptToLocal.x, -cptToLocal.y, -cptToLocal.z);
    let temp: NVector3[] = NVector3.MoveVecs(vecs, cptToWorld);
    temp = NVector3.RotateVecs(temp, angle);
    temp = NVector3.MoveVecs(temp, cptToLocal);
    return temp;
}
export const RotateVecs = (vecs: NVector3[], angle: number) => {
    let temp: NVector3[] = [];
    for (let i = 0; i < vecs.length; ++i) {
        temp.push(NVector3.RotateVec(vecs[i], angle));
    }
    return temp;
}
export const RotateVec = (v: NVector3, angle: number): NVector3 => {
    let cos = Math.cos(angle * (3.14159265358979 / 180.0));
    let sin = Math.sin(angle * (3.14159265358979 / 180.0));
    let x = v.x * cos - v.y * sin;
    let y = v.x * sin + v.y * cos;
    return new NVector3(x, y, v.z);
}