import * as THREE from 'three';
import { Demo300GA } from '..';
import { ActiveCommandController } from './ActiveCommand/ActiveCommandController';
import { GeometryCommon } from './GeometryCommon/GeometryCommon';
import { LightingController } from './Light/LightingController';
import { Definition } from './Definition';
export class SceneWrapper {
    app: Demo300GA;
    activeCommandController: ActiveCommandController;
    geometryCommon: GeometryCommon;
    lightingController: LightingController;

    scene: THREE.Scene;
    definition: Definition;

    constructor(app: Demo300GA) {
        this.app = app;
        this.activeCommandController = new ActiveCommandController(this);
        this.scene = this.app.graphicsCoreWrapper.canvasWrapper.canvasThree.scene;
        this.app.graphicsCoreWrapper.canvasWrapper.canvasThree.UpdateBinding = () => { this.Update(); };
        this.lightingController = new LightingController(this);
        this.geometryCommon = new GeometryCommon(this);
        this.Init();
    }
    Init() {
        this.definition = new Definition(this.scene);

        const size = 10;
        const divisions = 10;
        const colorGrid = 0xeeeeee;
        const colorCenterLine = 0xcccccc;
        const gridHelper = new THREE.GridHelper(size, divisions, colorCenterLine, colorGrid);
        this.scene.add(gridHelper);
    }
    Update() {
        this.definition.update();
    }
    MouseMove(x: number, y: number) {
        //    const pos =  this.geometryCommon.RaycastPoint(x, y, this.definition.points.pointCloud );
        // const pos = this.geometryCommon.RaycastPoint(x, y, this.definition.GetPointForRaycast());
        // if (pos) {
        // this.definition.mouseMove(pos[0], pos[1], pos[2]);
        // }
        this.definition.mouseMove(x, y, 0);
    }
    MouseDown(x: number, y: number) {
        // const pos = this.geometryCommon.RaycastPoint(x, y, this.definition.GetPointForRaycast());
        // if (pos) {
        this.app.graphicsCoreWrapper.canvasWrapper.canvasThree.camera.ableToMove = false;
        // this.definition.mouseDown(pos[0], pos[1], pos[2]);
        // }
        this.definition.mouseDown(x, y, 0);
    }
    MouseUp(x: number, y: number) {
        this.definition.mouseUp();
        this.app.graphicsCoreWrapper.canvasWrapper.canvasThree.camera.ableToMove = true;
    }
    MouseDrag(x: number, y: number) {
        // const pos = this.geometryCommon.RaycastPoint(x, y, this.definition.GetPointForRaycast());
        // if (pos) {
        // this.definition.mouseDrag(pos[0], pos[1]);
        // }
        this.definition.mouseDrag(x, y);
    }
    Dispose() {
        this.definition.dispose();
    }
}