
import { NVector3 } from '../../../../../lib';
import { Cell } from './Cell';
import { Optimizer } from '..';
export class GridSystem {
    optimizer: Optimizer;
    min: NVector3;
    max: NVector3;
    rx: number;
    ry: number;
    intervalX: number;
    intervalY: number;

    pts: NVector3[] = [];
    cells: Cell[][] = [];
    constructor(optimizer: Optimizer, min: NVector3, max: NVector3) {
        this.optimizer = optimizer;
        this.min = min;
        this.max = max;
    }
    public Init(rx: number, ry: number) {
        this.rx = rx;
        this.ry = ry;
        this.intervalX = (this.max.x - this.min.x) / (this.rx - 1);
        this.intervalY = (this.max.y - this.min.y) / (this.ry - 1);
        this.cells = [];
        this.InitCell();
        this.Rotate();
        this.UpdateInside();
    }
    public InitCell() {
        for (let j = 0; j < this.ry; ++j) {
            const y = this.min.y + j * this.intervalY;
            const cellList: Cell[] = [];
            for (let i = 0; i < this.rx; ++i) {
                const x = this.min.x + i * this.intervalX;
                const v = new NVector3(x, y);
                this.pts.push(v);
                cellList.push(new Cell(this, i, j, v, this.intervalX, this.intervalY));
            }
            this.cells.push(cellList);
        }
    }
    public Rotate() {
        for (let j = 0; j < this.ry; ++j) {
            for (let i = 0; i < this.rx; ++i) {
                const cell = this.cells[j][i];
                cell.Rotate(15);
            }
        }
    }
    public UpdateInside() {
        for (let j = 0; j < this.ry; ++j) {
            for (let i = 0; i < this.rx; ++i) {
                const cell = this.cells[j][i];
                if (NVector3.IsInsideOfVecsWtihVecs(cell.rect.vertices, this.optimizer.polyline.vertices)) {
                    cell.rect.color = 'black';
                    cell.v.color = 'black';
                    cell.isInside = true;
                } else {
                    cell.rect.color = '#efefef';
                    cell.v.color = 'efefef';
                    cell.isInside = false;

                    cell.rect.vertices.forEach((v: NVector3) => {
                        v.x -= 0.1;
                    });
                }
            }
        }
    }
}
