import { Definition } from '..';
import { Optimizer } from './Optimizer';
import { Polyline } from '../Common/Geometry/Polyline';
export class AppRectOptimizer {
    definition: Definition;
    optimizer: Optimizer;
    polyline: Polyline;
    constructor(definition: Definition) {
        this.definition = definition;
        this.AddPolyline();

        this.Init();
    }
    public AddPolyline() {
        this.polyline = Polyline.GetPolylineTemp();
        this.definition.renderer.AddPolyline(this.polyline.GetVerticesForThree());
    }
    public Init() {
        this.optimizer = new Optimizer(this);
    }
}



