import { AppGridOptimizer } from '../AppGridOptimizer';
import { Polyline } from '../../Common/Geometry/Polyline';
import { GridSystem } from './GridSystem/GridSystem';

export class Optimizer {
    gridOptimizerApp: AppGridOptimizer;
    rx: number;
    ry: number;
    polyline: Polyline;
    gridSystem: GridSystem;
    constructor(gridOptimizerApp: AppGridOptimizer, rx: number, ry: number) {
        this.gridOptimizerApp = gridOptimizerApp;
        this.rx = rx;
        this.ry = ry;

        this.InitPolyline();
        this.InitGridSyste();
    }
    public InitPolyline() {
        this.polyline = Polyline.GetPolylineTemp();

    }
    public InitGridSyste() {
        this.gridSystem = new GridSystem(this, this.polyline.boundingBox.min, this.polyline.boundingBox.max);
        this.gridSystem.Init(this.rx, this.ry);
        // console.log(this.gridSystem);
    }
}