import { RotateVecs } from '../Core/Math';
import { NVector3 } from '../../../../../lib';
import { Rect } from '../Geometry/Rect';
import { GridSystem } from './GridSystem';
export class Cell {
    grid: GridSystem;
    i: number;
    j: number;
    v: NVector3;
    width: number;
    height: number;
    rect: Rect;
    isInside: boolean = true;
    constructor(grid: GridSystem, i: number, j: number, v: NVector3, width: number, height: number) {
        this.grid = grid;
        this.i = i;
        this.j = j;
        this.v = v;
        this.width = width;
        this.height = height;
        this.rect = new Rect(
            new NVector3(this.v.x - this.width * 0.5, this.v.y - this.height * 0.5),
            new NVector3(this.v.x + this.width * 0.5, this.v.y - this.height * 0.5),
            new NVector3(this.v.x + this.width * 0.5, this.v.y + this.height * 0.5),
            new NVector3(this.v.x - this.width * 0.5, this.v.y + this.height * 0.5)
        );
    }
    public GetVerticesForThree() {
        // if (this.isInside) {
        return this.rect.GetVerticesForThree();
        // } else {
        //     return undefined;
        // }
    }
    public Rotate(v: number) {
        RotateVecs([this.v, ...this.rect.vertices], v);
    }
    public get color() {
        return this.rect.color;
    }
}