import { NVector3 } from '../../../../../lib';

export class BoundingBox {
    min: NVector3 = new NVector3(Number.MAX_SAFE_INTEGER, Number.MAX_SAFE_INTEGER, Number.MAX_SAFE_INTEGER);
    max: NVector3 = new NVector3(Number.MIN_SAFE_INTEGER, Number.MIN_SAFE_INTEGER, Number.MIN_SAFE_INTEGER);
    constructor() {

    }
    public Update(vs: NVector3[]) {
        this.min = new NVector3(Number.MAX_SAFE_INTEGER, Number.MAX_SAFE_INTEGER, Number.MAX_SAFE_INTEGER);
        this.max = new NVector3(Number.MIN_SAFE_INTEGER, Number.MIN_SAFE_INTEGER, Number.MIN_SAFE_INTEGER);

        for (let i = 0; i < vs.length; ++i) {
            if (vs[i].x < this.min.x) { this.min.x = vs[i].x; }
            if (vs[i].y < this.min.y) { this.min.y = vs[i].y; }
            if (vs[i].z < this.min.z) { this.min.z = vs[i].z; }

            if (vs[i].x > this.max.x) { this.max.x = vs[i].x; }
            if (vs[i].y > this.max.y) { this.max.y = vs[i].y; }
            if (vs[i].z > this.max.z) { this.max.z = vs[i].z; }
        }
    }
}