
import { BoundingBox } from './BoundingBox';
import { NVector3 } from '../../../../../../../../lib/NJSCore';

export class Rect {
    // p0: NVector3;
    // p1: NVector3;
    // p2: NVector3;
    // p3: NVector3;
    boundingBox: BoundingBox;
    vertices: NVector3[] = [];
    color: string = 'green';
    constructor(p0: NVector3, p1: NVector3, p2: NVector3, p3: NVector3) {
        // this.p0 = p0;
        // this.p1 = p1;
        // this.p2 = p2;
        // this.p3 = p3;
        this.vertices = [p0, p1, p2, p3];
    }
    public GetVerticesForThree() {
        return [
            [this.vertices[0].x, this.vertices[0].z, this.vertices[0].y],
            [this.vertices[1].x, this.vertices[1].z, this.vertices[1].y],
            [this.vertices[2].x, this.vertices[2].z, this.vertices[2].y],
            [this.vertices[3].x, this.vertices[3].z, this.vertices[3].y],
            [this.vertices[0].x, this.vertices[0].z, this.vertices[0].y],
        ];
    }
}